import { Field, InputType } from "@nestjs/graphql";
import { IsNotEmpty, IsOptional, IsString, IsUUID } from "class-validator";
import { Role } from "../../../../orm-config";
import { ColumnExists } from "../../../../validation";


@InputType()
export class CreateRoleInput {

  @Field(() => String)
  @IsString()
  @IsNotEmpty()
  @ColumnExists(Role)
  name: string;

  @Field(() => [String], { nullable: true })
  @IsUUID(4, { each: true })
  @IsOptional()
  actions: string[];

}
